'use strict';
import 'core-js/es/map';

import { Provider } from 'react-redux'
import { createStore } from 'redux'
import rootReducer from './reducers'

import 'core-js/es/set';
import React from 'react';
import ReactDOM from 'react-dom';
import connect from '@vkontakte/vkui-connect';
import App from './App';
import mVKMiniAppsScrollHelper from '@vkontakte/mvk-mini-apps-scroll-helper';
import Persik from './panels/Persik';

import registerServiceWorker from './sw';


connect.subscribe((e) => {
    switch (e.detail.type) {
        case 'VKWebAppStorageGetResult':
                localStorage.setItem("fullmap",e.detail.data.keys[0].value)
            break;
        case 'VKWebAppUpdateConfig':



            if(document.body.attributes.getNamedItem("scheme")==null){
                console.log("change scheme")
                let schemeAttribute = document.createAttribute('scheme');

                if(e.detail.data.scheme==="client_light"){
                    e.detail.data.scheme="bright_light"
                }
                if(e.detail.data.scheme==="client_dark"){
                    e.detail.data.scheme="space_gray"
                }

                schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'bright_light';
                document.body.attributes.setNamedItem(schemeAttribute);
                schemeAttribute= document.createAttribute('default_theme');
                schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'bright_light';
                document.body.attributes.setNamedItem(schemeAttribute);
                schemeAttribute=document.createAttribute('schemecolor');

                //  schemeAttribute.value = e.detail.data.scheme === "client_light" ? "white" : '#2c2c2e';
                // schemeAttribute.value = e.detail.data.scheme === "bright_light" ? "white" : '#2c2c2e';

                if(e.detail.data.scheme === "client_light" || e.detail.data.scheme === "bright_light"){
                    schemeAttribute.value="white"
                }else{
                    schemeAttribute.value="#2c2c2e"
                }

                document.body.attributes.setNamedItem(schemeAttribute);
            }
            if(document.body.attributes.getNamedItem("scheme")!==null) {
                if(document.body.attributes.getNamedItem("scheme").value==="client_light"){
                    document.body.attributes.getNamedItem("scheme").value="bright_light"
                }
                if(document.body.attributes.getNamedItem("scheme").value==="client_dark"){
                    document.body.attributes.getNamedItem("scheme").value="space_gray"
                }

                switch (document.body.attributes.getNamedItem("scheme")) {
                    case 'client_light':
                        document.body.attributes.getNamedItem("scheme").value = "client_light"

                        document.body.attributes.getNamedItem("schemecolor").value = "white"
                        connect.send("VKWebAppSetViewSettings", {
                            "status_bar_style": "light",
                            "action_bar_color": "#5281B9"
                        });

                        break;
                    case 'client_dark':
                        document.body.attributes.getNamedItem("scheme").value = "client_dark"



                        document.body.attributes.getNamedItem("schemecolor").value = "#2c2c2e"

                        connect.send("VKWebAppSetViewSettings", {
                            "status_bar_style": "light",
                            "action_bar_color": "#2C2D2F"
                        });

                        break;
                    case 'bright_light':
                        document.body.attributes.getNamedItem("scheme").value = "bright_light"



                        document.body.attributes.getNamedItem("schemecolor").value = "white"

                        connect.send("VKWebAppSetViewSettings", {
                            "status_bar_style": "dark",
                            "action_bar_color": "#fff"
                        });

                        break;
                    case 'space_gray':
                        document.body.attributes.getNamedItem("scheme").value = "space_gray"



                        document.body.attributes.getNamedItem("schemecolor").value = "#2c2c2e"

                        connect.send("VKWebAppSetViewSettings", {
                            "status_bar_style": "light",
                            "action_bar_color": "#191919"
                        });

                        break;
                }

            }



            break;

        default:
            console.log("Start "+e.detail.type);
    }
});





// Init VK App

connect.send('VKWebAppInit', {});
connect.send("VKWebAppStorageGet", {"keys": ["fullmap"]});
// –ï—Å–ª–∏ –≤—ã —Ö–æ—Ç–∏—Ç–µ, —á—Ç–æ–±—ã –≤–∞—à–µ –≤–µ–±-–ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ —Ä–∞–±–æ—Ç–∞–ª–æ –≤ –æ—Ñ—Ñ–ª–∞–π–Ω–µ –∏ –∑–∞–≥—Ä—É–∂–∞–ª–æ—Å—å –±—ã—Å—Ç—Ä–µ–µ,
// —Ä–∞—Å—Å–∫–æ–º–º–µ–Ω—Ç–∏—Ä—É–π—Ç–µ —Å—Ç—Ä–æ–∫—É —Å registerServiceWorker();
// –ù–æ –Ω–µ –∑–∞–±—ã–≤–∞–π—Ç–µ, —á—Ç–æ –Ω–∞ –¥–∞–Ω–Ω—ã–π –º–æ–º–µ–Ω—Ç —É —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–∏ –µ—Å—Ç—å –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –ø–æ–¥–≤–æ–¥–Ω—ã—Ö –∫–∞–º–Ω–µ–π
// –ü–æ–¥—Ä–æ–±–Ω–µ–µ –ø—Ä–æ —Å–µ—Ä–≤–∏—Å –≤–æ—Ä–∫–µ—Ä—ã –º–æ–∂–Ω–æ –ø–æ—á–∏—Ç–∞—Ç—å —Ç—É—Ç ‚Äî https://vk.cc/8MHpmT
registerServiceWorker();

const root = document.getElementById('root');

// –í—ã–∑—ã–≤–∞–µ–º —Ö–µ–ª–ø–µ—Ä, –ø–µ—Ä–µ–¥–∞–≤–∞—è –≤ –∞—Ä–≥—É–º–µ–Ω—Ç –∫–æ—Ä–Ω–µ–≤–æ–π —ç–ª–µ–º–µ–Ω—Ç –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
mVKMiniAppsScrollHelper(root);

// –†–µ–Ω–¥–µ—Ä –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
ReactDOM.render(<App />, root);
